import { useContext } from "react";
import { NavLink } from "react-router-dom";
import classes from "./MainNavigation.module.css";
import FavoriteContext from "../../store/favorite-context";
const MainNavigation = () => {
  const favCtx = useContext(FavoriteContext);
  return (
    <header className={classes.header}>
      <div className={classes.logo}>Meetups App</div>
      <nav>
        <ul>
          <li>
            <NavLink exact activeClassName={classes.active} to="/">
              All Meetups
            </NavLink>
          </li>
          <li>
            <NavLink exact activeClassName={classes.active} to="/favorites">
              Favorites{" "}
              <span className={classes.badge}>{favCtx.totalFavorites}</span>
            </NavLink>
          </li>
          <li>
            <NavLink exact activeClassName={classes.active} to="/new-meetup">
              New Meetup
            </NavLink>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default MainNavigation;
