import Card from "../UI/Card";
import classes from "./MeetupForm.module.css";
import { useRef } from "react";
import MeetupList from "./MeetupList";

const MeetupForm = (props) => {
  const imageRef = useRef();
  const titleRef = useRef();
  const descriptionRef = useRef();
  const addressRef = useRef();
  const submitHandler = (e) => {
    e.preventDefault();
    const imageInput = imageRef.current.value;
    const titleInput = titleRef.current.value;
    const descriptionInput = descriptionRef.current.value;
    const addressInput = addressRef.current.value;
    const inputData = {
      title: titleInput,
      image: imageInput,
      description: descriptionInput,
      address: addressInput,
    };
    console.log(inputData);
    props.onAddMeetup(inputData);
  };
  return (
    <Card>
      <form className={classes.form} onSubmit={submitHandler}>
        <div className={classes.control}>
          <label htmlFor="title">title</label>
          <input type="text" id="title" ref={titleRef} />
        </div>

        <div className={classes.control}>
          <label htmlFor="Image">Image</label>
          <input type="url" id="image" ref={imageRef} />
        </div>
        <div className={classes.control}>
          <label htmlFor="address">Address</label>
          <input type="text" id="address" ref={addressRef} />
        </div>
        <div className={classes.control}>
          <label htmlFor="description">Description</label>
          <textarea id="description" ref={descriptionRef}></textarea>
        </div>
        <div className={classes.actions}>
          <button>Add Meetup</button>
        </div>
      </form>
    </Card>
  );
};

export default MeetupForm;
